.product-item {
  position: relative;
  padding-top: 140px;
  width: 100%;
  height: 100%;
}

%product-box {
  width: 100%;
  height: 100%;
  padding-right: 40px;
  padding-bottom: 40px;
  padding-left: 40px;
  padding-top: 160px;
  border-radius: 24px;
  text-align: center;
}

.product-box-first {
  @extend %product-box;

  background-color: $bg-hero;
}

.product-box-second {
  @extend %product-box;

  background-color: $accent-yellow;
}

.product-box-third {
  @extend %product-box;

  background-color: $accent-green;
}

.product-box-first::before {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  content: '';
  display: block;
  width: 280px;
  height: 280px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url('../images/mobile/product1-img-mobile.png');
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/mobile/product1-img-mobile@2x.png');
  }
}

