  }
}

.product-title {
  font-family: $title-font;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $text-white;

  &::after {
    display: block;
    content: '';
    width: 26px;
    height: 6px;
    margin: 32px auto;
    background-image: url(../images/icons/Dots-product.svg);
    background-repeat: no-repeat;
  }
}
.product-text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.04em;
  color: $text-white;
}

.button-circle-large {
  margin-top: 32px;
  margin-right: auto;
  margin-left: auto;
}
